html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.logo {
  display: flex;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  text-align: center;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  display: inline-block;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

